
import { Options, Vue } from 'vue-class-component';
import HelloWorld from '@/components/HelloWorld.vue'; // @ is an alias to /src

@Options({
    components: {
        HelloWorld,
    },
    data() {
        return {
            data: [
                {
                    label: 'Tuần 1',
                    children: [
                        {
                            label: 'Bài 1.1',
                            path: '/bai1_1',
                        },
                        {
                            label: 'Bài 1.2',
                            path: '/bai1_2',
                        },
                    ],
                },
                {
                    label: 'Tuần 2',
                    children: [
                        {
                            label: 'Bài 2',
                            path: '/bai2',
                        },
                    ],
                },
            ],
            defaultProps: {
                children: 'children',
                label: 'label',
            },
        };
    },
})
export default class HomePage extends Vue {}
